import React from 'react';
import { Button, Form, FormGroup, Label, Input, Row, Col } from 'reactstrap';

class TypeItIn extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      initialsName: '',
      nameFont: ''
    };
  }
  render() {
    return (
      <>
        <Row>
          <Col sm="12">
            <h2 className="text-center">Create your Signature</h2>
          </Col>
        </Row>
        <Form>
          <Row style={{ marginBottom: '30px' }}>
            <Col>
              <FormGroup>
                <Label for="confirm-name" style={{ marginBottom: '15px' }}>
                  Confirm your name
                </Label>
                <Input
                  type="text"
                  name="name"
                  id="confirm-name"
                  placeholder="Confirm your name"
                  onChange={event =>
                    this.setState({ name: event.target.value })
                  }
                />
              </FormGroup>
            </Col>
            <Col sm="4">
              <FormGroup>
                <Label for="confirm-initials" style={{ marginBottom: '15px' }}>
                  Confirm your initials
                </Label>
                <Input
                  type="text"
                  name="initials"
                  id="confirm-initials"
                  placeholder="Confirm your initials"
                  onChange={event =>
                    this.setState({ initialsName: event.target.value })
                  }
                />
              </FormGroup>
            </Col>
          </Row>
          <Row>
            <Col>
              <Label for="confirm-name" style={{ marginBottom: '15px' }}>
                Preview
              </Label>
            </Col>
          </Row>
          <Row style={{ marginBottom: '30px' }}>
            <Col>
              <FormGroup>
                <span
                  style={{
                    border: '12px solid #CCCCCC',
                    background: '#ffffff',
                    padding: '8px 15px',
                    display: 'block',
                    fontWeight: 'bolder',
                    fontSize: '50px',
                    minHeight: '132px',
                    fontFamily: `${this.state.nameFont}`
                  }}
                  className="name-preview"
                >
                  {this.state.name}
                </span>
              </FormGroup>
            </Col>
            <Col sm="4">
              <FormGroup>
                <span
                  style={{
                    border: '12px solid #CCCCCC',
                    background: '#ffffff',
                    padding: '8px 15px',
                    display: 'inline-block',
                    fontWeight: 'bolder',
                    fontSize: '50px',
                    minHeight: '132px',
                    minWidth: '200px',
                    fontFamily: `${this.state.nameFont}`
                  }}
                  className="initials-preview"
                >
                  {this.state.initialsName}
                </span>
              </FormGroup>
            </Col>
          </Row>

          <Row>
            <Col>
              <p>
                By clicking Accept and Sign, I agree that the signature wil be
                the electric representation of my signature for all purposewhen
                I for my agend use themon documents. Including legallybinding
                documnets, just the same as pen and paper signature.{' '}
              </p>
            </Col>
          </Row>

          <Row>
            <Col>
              <FormGroup style={{ display: 'flex' }}>
                <Label
                  for="change-font"
                  style={{ flex: 3, marginBottom: '15px' }}
                >
                  Change Font:
                </Label>
                <select
                  className="form-control"
                  style={{ flex: 9 }}
                  id="change-font"
                  onChange={event =>
                    this.setState({ nameFont: event.target.value })
                  }
                >
                  <option value="Big Caslon">Big Caslon</option>
                  <option value="Times New Roman">Times New Roman</option>
                  <option value="Arial">Arial</option>
                  <option value="Roboto">Roboto</option>
                  <option value="Times">Times</option>
                  <option value="Courier New">Courier New</option>
                  <option value="Georgia">Georgia</option>
                  <option value="Palatino">Palatino</option>
                  <option value="Garamond">Garamond</option>
                  <option value="Candara">Candara</option>
                  <option value="Arial Black">Arial Black</option>
                  <option value="Impact">Impact</option>
                </select>
              </FormGroup>
            </Col>
            <Col sm="4">
              <FormGroup>
                <Button color="primary">Accept and Sign</Button>
              </FormGroup>
            </Col>
          </Row>
        </Form>
      </>
    );
  }
}

export default TypeItIn;
